/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react';
import { Formik } from 'formik';
import { useSelector, useDispatch } from 'react-redux';
import * as Yup from 'yup';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { addBankDetailSaga } from '../../../store/actions';
import {
  NAME_REQUIRED,
  // GST_REQUIRED,
  ACCOUNT_NUMBER_REQUIRED,
  ACCOUNT_NUMBER_NUMBER,
  // SWIFT_CODE_REQUIRED,
  ACCOUNT_HOLDER_REQUIRED,
  IFSC_REQUIRED,
} from '../../../constants/errorConstants';

const BankingDetails = props => {
  const { setPageName } = props;
  const [isDisable, setIsDisable] = useState(false);
  const {
    bankDetail,
    isApproved,
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.chef);
  const [initialValues, setInitialValues] = useState({
    name: bankDetail.name || '',
    accountNumber: bankDetail.accountNumber || '',
    gstNumber: bankDetail.gstNumber || '',
    swiftCode: bankDetail.swiftCode || '',
    accountHolder: bankDetail.accountHolder || '',
    ifscCode: bankDetail.ifscCode || '',
  });

  const dispatch = useDispatch();

  const validationSchema = Yup.object({
    name: Yup.string().required(NAME_REQUIRED),
    accountNumber: Yup.number()
      .typeError(ACCOUNT_NUMBER_NUMBER)
      .required(ACCOUNT_NUMBER_REQUIRED),
    gstNumber: Yup.string(),
    swiftCode: Yup.string(),
    accountHolder: Yup.string().required(ACCOUNT_HOLDER_REQUIRED),
    ifscCode: Yup.string().required(IFSC_REQUIRED),
  });

  return (
    <div className="row">
      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
        <Formik
          enableReinitialize
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            setIsDisable(true);
            dispatch(
              addBankDetailSaga({
                values,
                setIsDisable,
                isApproved,
                setPageName,
              }),
            );
            setInitialValues({
              name: values.name || '',
              accountNumber: values.accountNumber || '',
              gstNumber: values.gstNumber || '',
              swiftCode: values.swiftCode || '',
              accountHolder: values.accountHolder || '',
              ifscCode: values.ifscCode || '',
            });
            setSubmitting(false);
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            resetForm,
            // isSubmitting,
          }) => (
            <form onSubmit={handleSubmit}>
              <div className="ibox">
                <div className="ibox-content">
                  {/* <div className="row m-b-lg">
                    <div className="col-xs-12">
                      <h2 className="">Banking Details</h2>
                    </div>
                  </div> */}

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Bank Name
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="name"
                            value={values.name}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.name && touched.name && errors.name}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Account Number
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="accountNumber"
                            value={values.accountNumber}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.accountNumber &&
                              touched.accountNumber &&
                              errors.accountNumber}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Account Holder Name
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="accountHolder"
                            value={values.accountHolder}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.accountHolder &&
                              touched.accountHolder &&
                              errors.accountHolder}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm">
                          SWIFT Code
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="swiftCode"
                            value={values.swiftCode}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.swiftCode &&
                              touched.swiftCode &&
                              errors.swiftCode}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          IFSC Code
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="ifscCode"
                            value={values.ifscCode}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.ifscCode &&
                              touched.ifscCode &&
                              errors.ifscCode}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm">
                          GST Number
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="gstNumber"
                            value={values.gstNumber}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.gstNumber &&
                              touched.gstNumber &&
                              errors.gstNumber}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-12 error-message">{errorMsg}</div>
                  </div>

                  <div className=" text-center">
                    {!isApproved && (
                      <button
                        type="button"
                        disabled={isDisable}
                        className="btn btn-primary m-5"
                        onClick={() => setPageName('Personal')}
                      >
                        Previous
                      </button>
                    )}
                    <button
                      type="submit"
                      disabled={isDisable}
                      className="btn btn-primary m-5"
                    >
                      {isApproved ? 'Update' : 'Save & Next'}
                    </button>
                    {isApproved && (
                      <button
                        type="button"
                        disabled={isDisable}
                        className="btn btn-primary"
                        onClick={() => resetForm()}
                      >
                        Reset
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </form>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default SetTokenHeader(BankingDetails, axios);
