/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { useSelector } from 'react-redux';
import moment from 'moment';
// eslint-disable-next-line no-unused-vars
const PayoutDetails = props => {
  const {
    chef,
    // eslint-disable-next-line no-unused-vars
    isApproved,
  } = useSelector(state => state.chef);
  return (
    <div className="row">
      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
        <div className="ibox">
          <div className="ibox-content">
            {/* <div className="row m-b-lg">
              <div className="col-xs-12">
                <h2 className="">Payout Details</h2>
              </div>
            </div> */}

            <div className="row">
              <div className="form-group usr-form col-sm-6">
                <div className="row">
                  <label className="col-sm-5 col-xs-12 m-t-sm">
                    Commission(%)
                  </label>
                  <div className="col-sm-7 col-xs-12 m-t-sm">
                    <b>
                      {chef.commissionPercentage
                        ? chef.commissionPercentage
                        : '0'}
                    </b>
                  </div>
                </div>
              </div>
              <div className="form-group usr-form col-sm-6" />
              <div className="form-group usr-form col-sm-6">
                <div className="row">
                  <label className="col-sm-5 col-xs-12 m-t-sm">
                    Total Payout
                  </label>
                  <div className="col-sm-7 col-xs-12 m-t-sm">
                    <b>{chef.payout ? chef.payout.totalPaid : '0'}</b>
                  </div>
                </div>
              </div>
              <div className="form-group usr-form col-sm-6">
                <div className="row">
                  <label className="col-sm-5 col-xs-12 m-t-sm">
                    Total Pending Payout
                  </label>
                  <div className="col-sm-7 col-xs-12 m-t-sm">
                    <b>{chef.payout ? chef.payout.totalPending : '0'}</b>
                  </div>
                </div>
              </div>
              <div className="form-group usr-form col-sm-6">
                <div className="row">
                  <label className="col-sm-5 col-xs-12 m-t-sm">
                    Last Payout Date
                  </label>
                  <div className="col-sm-7 col-xs-12 m-t-sm">
                    <b>
                      {chef.payout
                        ? moment(chef.payout.lastPayoutDate).format('L hh:mm A')
                        : '-'}
                    </b>
                  </div>
                </div>
              </div>
              <div className="form-group usr-form col-sm-6">
                <div className="row">
                  <label className="col-sm-5 col-xs-12 m-t-sm">
                    Last Payout
                  </label>
                  <div className="col-sm-7 col-xs-12 m-t-sm">
                    <b>{chef.payout ? chef.payout.lastPayout : '0'}</b>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PayoutDetails;
