/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { getChefProfileSaga, resetErrorMsg } from '../../../store/actions';
import PersonalDetails from './PersonalDetails';
import BankingDetails from './BankingDetails';
import RestaurantDetails from './RestaurantDetails';
import PayoutDetails from './PayoutDetails';
import OrderSchedule from './OrderSchedule';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import MenuItems from '../MenuItems/MenuItems';

// eslint-disable-next-line no-unused-vars
const Profile = props => {
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [pageName, setPageName] = useState('Restaurant');
  const [showReason, setShowReason] = useState(true);
  const {
    chef: { _id, restaurantName, address, pickup, rejectedReason },
    bankDetail: { name },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
    isApproved,
    isProfileSubmitted,
    isRejected,
  } = useSelector(state => state.chef);
  const { open } = useSelector(state => state.modal);
  // const address = [];

  const dispatch = useDispatch();

  useEffect(() => {
    setMounted(true);
    dispatch(getChefProfileSaga());
    return () => setMounted(false);
  }, []);

  useEffect(() => {
    if (mounted) {
      dispatch(resetErrorMsg());
    }
  }, [pageName]);

  const handleTabChange = page => {
    // dispatch(resetErrorMsg());
    setPageName(page);
  };
  // const genrateAddress = () => {
  //   if (chef.restaurantAddress) {
  //     const { restaurantAddress } = chef;
  //     const {
  //       apartment,
  //       area,
  //       city,
  //       houseNo,
  //       landmark,
  //       pincode,
  //       street,
  //     } = restaurantAddress;
  //     if (houseNo) {
  //       address.push(houseNo);
  //     }
  //     if (apartment) {
  //       address.push(apartment);
  //     }
  //     if (landmark) {
  //       address.push(landmark);
  //     }
  //     if (area) {
  //       address.push(area);
  //     }
  //     if (city) {
  //       address.push(city);
  //     }
  //     if (street) {
  //       address.push(street);
  //     }
  //     if (pincode) {
  //       address.push(pincode);
  //     }
  //   }
  // };

  // if (chef._id !== undefined) {
  //   genrateAddress();
  // }

  return (
    <div id="profile">
      <div
        className={
          isApproved
            ? 'wrapper wrapper-content tab-justify'
            : 'wrapper wrapper-content container tab-justify'
        }
        id="tab-new1"
      >
        {isApproved === false &&
          isProfileSubmitted === false &&
          isRejected === true &&
          rejectedReason &&
          showReason &&
          sessionStorage.getItem('showReason') !== 'false' && (
            <div
              className="alert alert-danger"
              role="alert"
              style={{ textAlign: 'justify', fontSize: '15px' }}
            >
              <p>{rejectedReason}</p>
              <div
                style={{
                  textAlign: 'right',
                }}
              >
                <span
                  style={{ cursor: 'pointer', color: 'inherit' }}
                  onClick={() => {
                    setShowReason(false);
                    sessionStorage.setItem('showReason', false);
                  }}
                >
                  Click here to close
                </span>
              </div>
            </div>
          )}
        {!isApproved && isProfileSubmitted ? (
          <div className="tab-content" style={{ marginTop: 30 }}>
            <div className="row">
              <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                <div className="ibox">
                  <div
                    className="ibox-content profile-under-review"
                    style={{ padding: '55px 0px' }}
                  >
                    <img
                      style={{ width: 150 }}
                      src={require('../../../assets/img/IconUnderReviewPage-01.jpg')}
                      alt=""
                    />
                    <h2
                      style={{
                        color: '#fcb459',
                        fontSize: '30px',
                        marginTop: '28px',
                      }}
                    >
                      Your profile is under review
                    </h2>
                    <p
                      style={{
                        fontSize: '21px',
                        padding: '10px 69px',
                        color: '#696969',
                      }}
                    >
                      Your profile has been submitted and will be reviewed by
                      our team. You will be notified if anything is needed.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <>
            <ul className="nav nav-tabs nav-justified">
              <li className={pageName === 'Restaurant' ? 'active' : ''}>
                <a
                  onClick={() =>
                    (isApproved || restaurantName !== '') &&
                    handleTabChange('Restaurant')
                  }
                >
                  Vendor Details
                </a>
              </li>
              <li className={pageName === 'Personal' ? 'active' : ''}>
                <a
                  onClick={() =>
                    (isApproved || address !== '') &&
                    handleTabChange('Personal')
                  }
                >
                  Personal Details
                </a>
              </li>
              <li className={pageName === 'Banking' ? 'active' : ''}>
                <a
                  onClick={() =>
                    (isApproved || name !== '') && handleTabChange('Banking')
                  }
                >
                  Banking Details
                </a>
              </li>
              <li className={pageName === 'Order' ? 'active' : ''}>
                <a
                  onClick={() =>
                    (isApproved || pickup !== '') && handleTabChange('Order')
                  }
                >
                  Order Schedule
                </a>
              </li>
              {!isApproved && (
                <li className={pageName === 'MenuItems' ? 'active' : ''}>
                  <a
                    onClick={() =>
                      (isApproved || name !== '') &&
                      handleTabChange('MenuItems')
                    }
                  >
                    Menu Items
                  </a>
                </li>
              )}
              {isApproved && (
                <li className={pageName === 'Payout' ? 'active' : ''}>
                  <a onClick={() => handleTabChange('Payout')}>
                    Payout Details
                  </a>
                </li>
              )}
            </ul>
            <div className="tab-content">
              {_id ? (
                <>
                  <div
                    className={
                      pageName === 'Personal'
                        ? 'tab-pane fade in active'
                        : 'tab-pane fade'
                    }
                  >
                    <PersonalDetails setPageName={setPageName} />
                  </div>
                  <div
                    className={
                      pageName === 'Banking'
                        ? 'tab-pane fade in active'
                        : 'tab-pane fade'
                    }
                  >
                    <BankingDetails setPageName={setPageName} />
                  </div>
                  <div
                    className={
                      pageName === 'Restaurant'
                        ? 'tab-pane fade in active'
                        : 'tab-pane fade'
                    }
                  >
                    <RestaurantDetails setPageName={setPageName} />
                  </div>
                  <div
                    className={
                      pageName === 'Payout'
                        ? 'tab-pane fade in active'
                        : 'tab-pane fade'
                    }
                  >
                    <PayoutDetails setPageName={setPageName} />
                  </div>
                  <div
                    className={
                      pageName === 'MenuItems'
                        ? 'tab-pane fade in active'
                        : 'tab-pane fade'
                    }
                  >
                    <MenuItems isProfilePage setPageName={setPageName} />
                  </div>
                  <div
                    className={
                      pageName === 'Order'
                        ? 'tab-pane fade in active'
                        : 'tab-pane fade'
                    }
                  >
                    <OrderSchedule setPageName={setPageName} />
                  </div>
                </>
              ) : (
                <div className="row">
                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <div className="ibox">
                      <div
                        className="ibox-content"
                        style={{ textAlign: 'center', padding: '200px 0px' }}
                      >
                        Loading...
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </>
        )}
      </div>
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(Profile, axios);
