import React, { useState } from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import 'yup-phone';
import { useSelector, useDispatch } from 'react-redux';
import ChangePasswordChefModal from './ChangePasswordChefModal';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import { editPersonalDetailSaga } from '../../../store/actions';
import {
  EMAIL_REQUIRED,
  EMAIL_VALID,
  FIRSTNAME_REQUIRED,
  LASTNAME_REQUIRED,
  PHONE_VALID,
  ADDRESS_REQUIRED,
  GENDER_REQUIRED,
} from '../../../constants/errorConstants';

const PersonalDetails = props => {
  const { setPageName } = props;
  const [modelOpen, setModelOpen] = useState(false);
  const [isDisable, setIsDisable] = useState(false);
  const {
    chef,
    // eslint-disable-next-line no-unused-vars
    isApproved,
    errorMsg,
  } = useSelector(state => state.chef);
  const [initialValues, setInitialValues] = useState({
    firstName: chef.firstName || '',
    lastName: chef.lastName || '',
    phone: chef.phone || '',
    email: chef.email || '',
    address: chef.address || '',
    gender: chef.gender || '',
  });
  const { open } = useSelector(state => state.modal);

  const dispatch = useDispatch();

  const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

  const validationSchema = Yup.object({
    firstName: Yup.string().required(FIRSTNAME_REQUIRED),
    lastName: Yup.string().required(LASTNAME_REQUIRED),
    phone: Yup.string().matches(phoneRegExp, PHONE_VALID).required(),
    email: Yup.string().email(EMAIL_VALID).required(EMAIL_REQUIRED),
    address: Yup.string().required(ADDRESS_REQUIRED),
    gender: Yup.string().required(GENDER_REQUIRED),
    // averagePrice: Yup.string().required('Required field'),
  });

  return (
    <div className="row">
      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
        <Formik
          enableReinitialize
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            setIsDisable(true);
            dispatch(
              editPersonalDetailSaga({
                values,
                setIsDisable,
                setPageName,
                isApproved,
              }),
            );
            setInitialValues({
              firstName: values.firstName || '',
              lastName: values.lastName || '',
              phone: values.phone || '',
              email: values.email || '',
              address: values.address || '',
              gender: values.gender || '',
            });
            setSubmitting(false);
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            resetForm,
            // isSubmitting,
          }) => (
            <form onSubmit={handleSubmit}>
              <div className="ibox">
                <div className="ibox-content">
                  <div className="row m-b-lg">
                    <div className="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                      {/* <h2 className="">Personal Details</h2> */}
                    </div>
                    <div className="col-lg-4 col-md-4 col-sm-4 col-xs-12 text-right">
                      <button
                        type="button"
                        data-toggle="modal"
                        className="btn btn-primary"
                        data-target="#modal-change-pw"
                        onClick={() => setModelOpen(true)}
                      >
                        <i className="fa fa-key" aria-hidden="true" />
                        Change Password
                      </button>
                    </div>
                  </div>

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          First name
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="firstName"
                            value={values.firstName}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.firstName &&
                              touched.firstName &&
                              errors.firstName}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Last Name
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="lastName"
                            value={values.lastName}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.lastName &&
                              touched.lastName &&
                              errors.lastName}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Email
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="email"
                            disabled
                            value={values.email}
                          />
                          <div className="error-message">
                            {errors.email && touched.email && errors.email}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Phone Number
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <input
                            type="number"
                            className="form-control"
                            placeholder=""
                            name="phone"
                            value={values.phone}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.phone && touched.phone && errors.phone}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Gender
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <select
                            className="form-control"
                            name="gender"
                            value={values.gender}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          >
                            <option value="">Select gender</option>
                            <option value="male">Male</option>
                            <option value="female">Female</option>
                          </select>
                          <div className="error-message">
                            {errors.gender && touched.gender && errors.gender}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-12">
                      <div className="row">
                        <label className="col-sm-3 col-xs-12 m-t-sm required-field">
                          Address
                        </label>
                        <div className="col-sm-9 col-xs-12">
                          {/* <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="address"
                            value={values.address}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          /> */}
                          <textarea
                            className="form-control"
                            style={{ height: '200px !important' }}
                            name="address"
                            value={values.address}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.address &&
                              touched.address &&
                              errors.address}
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* <div className="col-lg-6 col-md-6 col-sm-6">
                      <div className="form-group">
                        <label>Average Price</label>
                        <input
                          type="text"
                          className="form-control"
                          placeholder=""
                          name="averagePrice"
                          value={values.averagePrice}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <div className="error-message">
                          {errors.averagePrice &&
                            touched.averagePrice &&
                            errors.averagePrice}
                        </div>
                      </div>
                    </div> */}
                  </div>

                  <div className="row">
                    <div className="col-12 error-message">{errorMsg}</div>
                  </div>

                  <div className="text-center" style={{ marginTop: '20px' }}>
                    {!isApproved && (
                      <button
                        type="button"
                        disabled={isDisable}
                        className="btn btn-primary m-5"
                        onClick={() => setPageName('Restaurant')}
                      >
                        Previous
                      </button>
                    )}
                    <button
                      type="submit"
                      disabled={isDisable}
                      className="btn btn-primary m-5"
                    >
                      {isApproved ? 'Update' : 'Save & Next'}
                    </button>
                    {isApproved && (
                      <button
                        type="button"
                        disabled={isDisable}
                        className="btn btn-primary"
                        onClick={() => resetForm()}
                      >
                        Reset
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </form>
          )}
        </Formik>
        {modelOpen && <ChangePasswordChefModal setModelOpen={setModelOpen} />}
        {open && <AlertMessageModal />}
      </div>
    </div>
  );
};

export default SetTokenHeader(PersonalDetails, axios);
