/* eslint-disable eqeqeq */
/* eslint-disable no-restricted-globals */
const countryList = require('./country.json');
const stateList = require('./state.json');
const cityList = require('./city.json');

const getCountryById = id => {
  if (!isNaN(+id)) return countryList[+id];
  return '';
};
const getStateById = id => {
  if (!isNaN(+id)) return stateList[+id];
  return '';
};
const getStateByName = stateName => {
  if (stateName) return stateList.filter(item => item.name === stateName)[0];
  return '';
};
const getCityById = id => {
  if (!isNaN(+id)) return cityList[+id];
  return '';
};
const getCityByName = cityName => {
  if (cityName) return cityList.filter(item => item.name === cityName)[0];
  return '';
};
const getStatesOfCountry = countryId => {
  const states = stateList.filter(value => {
    return value.country_id == countryId;
  });
  return states;
};
const getCitiesOfState = stateId => {
  const cities = cityList.filter(value => {
    return value.state_id == stateId;
  });
  return cities;
};
const getAllCountries = () => {
  return countryList;
};

export {
  getCountryById,
  getStateById,
  getStateByName,
  getCityById,
  getCityByName,
  getStatesOfCountry,
  getCitiesOfState,
  getAllCountries,
};
