import React, { useState, useEffect } from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import 'yup-phone';
import Select from 'react-select';
import { useDispatch, useSelector } from 'react-redux';
import jQuery from 'jquery';
// import Select from 'react-select';
// import DatePicker from 'react-datepicker';
// import moment from 'moment';
import Rate from 'rc-rate';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import Map from '../../../components/UI/Map/Map';
import {
  deleteIdproofSaga,
  deleteMediaSaga,
  editIdproofSaga,
  editMediaSaga,
  editRestaurantDetailSaga,
  getAllBrandTypesSaga,
  getLatLongFromAddressSaga,
  resetLatLongFromAddress,
  showModal,
} from '../../../store/actions';

import 'react-datepicker/dist/react-datepicker.css';
import {
  RESTAURANT_CONTACT_VALID,
  RESTAURANT_NAME_REQUIRED,
  PAYMENT_OPTIONS_REQUIRED,
  ORDER_TYPE_REQUIRED,
  HOUSE_NUMBER_REQUIRED,
  // APARTMENT_REQUIRED,
  LANDMARK_REQUIRED,
  AREA_REQUIRED,
  CITY_REQUIRED,
  STATE_REQUIRED,
  COUNTRY_REQUIRED,
  PINCODE_REQUIRED,
  PINCODE_VALID,
} from '../../../constants/errorConstants';
import {
  getCitiesOfState,
  getStateByName,
  getStatesOfCountry,
} from '../../../utils/CSC';

// eslint-disable-next-line max-len
const idProofRegx = /(JPG|JPEG|PNG|PDF|DOC|DOCX|MSWORD|vnd.openxmlformats-officedocument.wordprocessingml.document|octet-stream)$/i;
const selectedBrands = [];
// eslint-disable-next-line no-unused-vars
const RestaurantDetails = props => {
  const [mounted, setMounted] = useState(false);
  const {
    chef,
    brandTypes,
    latitudeFromApi,
    longitudeFromApi,
    isMapLoading,
    isApproved,
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.chef);
  const [isDisable, setIsDisable] = useState(false);
  const [isReloadMap, setIsReloadMap] = useState(false);
  const [initialValues] = useState({
    restaurantName: chef.restaurantName || '',
    retsaurantDescription: chef.retsaurantDescription || '',
    retsaurantContact: chef.retsaurantContact || '',
    // startTime: chef.startTime || '',
    // endTime: chef.endTime || '',
    paymentOptions: chef.paymentOptions || 'both',
    orderType: chef.orderType || 'both',
    houseNo: chef.restaurantAddress.houseNo || '',
    apartment: chef.restaurantAddress.apartment || '',
    landmark: chef.restaurantAddress.landmark || '',
    area: chef.restaurantAddress.area || '',
    city: chef.restaurantAddress.city || '',
    state: chef.restaurantAddress.state || '',
    country: 'India',
    pincode: chef.restaurantAddress.pincode || '',
  });
  const [coordinates, setCoordinates] = useState({
    latitude: chef.lat || 28.7041,
    longitude: chef.long || 77.1025,
  });
  const [brandType, setBrandTypes] = useState([]);
  const [avgPrice, setAvgPrice] = useState(chef.avgPrice || 0);
  const [brandTypeError, setBrandTypeError] = useState(false);
  // const [startTime, setStartTime] = useState(() => {
  //   return chef.startTime || '09:00';
  // });
  // const [endTime, setEndTime] = useState(() => {
  //   return chef.endTime || '21:00';
  // });
  // const [timeError, setTimeError] = useState('');
  const [avgPriceError, setAvgPriceError] = useState('');
  const [isSubmit, setIsSubmit] = useState(false);
  const [stateList, setStateList] = useState([]);
  const [cityList, setCityList] = useState([]);

  const dispatch = useDispatch();

  useEffect(() => {
    setMounted(true);
    dispatch(
      getAllBrandTypesSaga({
        columnName: 0,
        orderBy: 'asc',
        search: '',
        skip: 0,
        limit: 5000,
      }),
    );
    setStateList(getStatesOfCountry(101));
    const stateObj = getStateByName(initialValues.state);
    setCityList(getCitiesOfState(stateObj.id));
    jQuery('[data-toggle="tooltip"]').tooltip();
    setBrandTypes(selectedBrands);
    return () => setMounted(false);
  }, []);

  // if (chef) {
  //   setBrandTypes(
  //     chef.brandTypeIds.split(', ').map(item => ({
  //       id: item,
  //       label: item,
  //       value: item,
  //     })),
  //   );
  // }

  useEffect(() => {
    if (mounted && latitudeFromApi !== '' && longitudeFromApi !== '') {
      setCoordinates({
        latitude: latitudeFromApi || '',
        longitude: longitudeFromApi || '',
      });
      dispatch(resetLatLongFromAddress());
      setIsReloadMap(true);
    }
  }, [latitudeFromApi, longitudeFromApi]);

  const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

  const validationSchema = Yup.object({
    restaurantName: Yup.string().required(RESTAURANT_NAME_REQUIRED),
    retsaurantDescription: Yup.string().required('Required field'),
    retsaurantContact: Yup.string()
      .matches(phoneRegExp, RESTAURANT_CONTACT_VALID)
      .required(),
    // startTime: Yup.string().required('Required field'),
    // endTime: Yup.string().required('Required field'),
    // openDays: Yup.string().required('Required field'),
    // brandType: Yup.string().required('Required field'),
    paymentOptions: Yup.string().required(PAYMENT_OPTIONS_REQUIRED),
    orderType: Yup.string().required(ORDER_TYPE_REQUIRED),
    houseNo: Yup.string().required(HOUSE_NUMBER_REQUIRED),
    apartment: Yup.string(),
    landmark: Yup.string().required(LANDMARK_REQUIRED),
    area: Yup.string().required(AREA_REQUIRED),
    city: Yup.string().required(CITY_REQUIRED),
    state: Yup.string().required(STATE_REQUIRED),
    country: Yup.string().required(COUNTRY_REQUIRED),
    pincode: Yup.string()
      .label('Pincode')
      .min(6, PINCODE_VALID)
      .max(6, PINCODE_VALID)
      .required(PINCODE_REQUIRED),
  });
  if (chef.brandTypeIds && selectedBrands.length === 0) {
    Object.values(brandTypes).filter(item => {
      if (chef.brandTypeIds.includes(item.value)) {
        selectedBrands.push({
          id: item.id,
          label: item.label,
          value: item.value,
        });
      }
    });
  }
  // const sorter = {
  //   sunday: 0,
  //   monday: 1,
  //   tuesday: 2,
  //   wednesday: 3,
  //   thursday: 4,
  //   friday: 5,
  //   saturday: 6,
  // };

  // const sortDays = data =>
  //   data.sort(function sortByDay(a, b) {
  //     const day1 = a.toLowerCase();
  //     const day2 = b.toLowerCase();
  //     return sorter[day1] - sorter[day2];
  //   });

  const getCoordinatesFromApi = (errorList, values) => {
    if (
      !(
        Object.prototype.hasOwnProperty.call(errorList, 'houseNo') &&
        Object.prototype.hasOwnProperty.call(errorList, 'apartment') &&
        Object.prototype.hasOwnProperty.call(errorList, 'landmark') &&
        Object.prototype.hasOwnProperty.call(errorList, 'area') &&
        Object.prototype.hasOwnProperty.call(errorList, 'city') &&
        Object.prototype.hasOwnProperty.call(errorList, 'state') &&
        Object.prototype.hasOwnProperty.call(errorList, 'country') &&
        Object.prototype.hasOwnProperty.call(errorList, 'pincode')
      )
    ) {
      const addressArr = [];

      if (values.houseNo.trim()) {
        addressArr.push(values.houseNo.trim());
      }
      if (values.apartment.trim()) {
        addressArr.push(values.apartment.trim());
      }
      if (values.landmark.trim()) {
        addressArr.push(values.landmark.trim());
      }
      if (values.area.trim()) {
        addressArr.push(values.area.trim());
      }
      if (values.city.trim()) {
        addressArr.push(values.city.trim());
      }
      if (values.state.trim()) {
        addressArr.push(values.state.trim());
      }
      if (values.country.trim()) {
        addressArr.push(values.country.trim());
      }
      if (values.pincode.trim()) {
        addressArr.push(values.pincode.trim());
      }

      dispatch(getLatLongFromAddressSaga(addressArr.join(',')));
    }
  };

  const handleSubmitEvent = values => {
    const { setPageName } = props;
    setIsDisable(true);
    const data = {
      ...values,
      brandType: brandType.map(a => a.id),
      ...coordinates,
      // openDays: sortDays(openDays.map(item => item.value)).join(', '),
      // startTime,
      // endTime,
      avgPrice,
    };
    dispatch(
      editRestaurantDetailSaga({ data, setIsDisable, isApproved, setPageName }),
    );
  };

  const getLatLng = coord => {
    setCoordinates(coord);
  };

  const checkData = () => {
    let isError = false;
    // if (!openDays || openDays.length === 0) {
    //   setOpenDaysError(true);
    //   isError = true;
    // }
    if (avgPrice === 0) {
      setAvgPriceError('Cost for two is require');
      isError = true;
    }
    if (chef.media.length === 0) {
      setIsSubmit(true);
      isError = true;
    }
    if (!isApproved && chef.idProof.length === 0) {
      setIsSubmit(true);
      isError = true;
    }
    // if (startTime === '') {
    //   setTimeError('Start time is reqiure');
    //   isError = true;
    // } else if (endTime === '') {
    //   setTimeError('End time is reqiure');
    //   isError = true;
    // } else if (moment(startTime, 'HH:mm') >= moment(endTime, 'HH:mm')) {
    //   setTimeError('Start time must be less than end time');
    //   isError = true;
    // }
    return isError;
  };

  return (
    <div className="row">
      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
        <Formik
          enableReinitialize
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            const isError = checkData();
            // if (openDays.length === 0) {
            //   setOpenDaysError(true);
            //   isError = true;
            // }
            // if (avgPrice === 0) {
            //   setAvgPriceError('Cost for two is require');
            //   isError = true;
            // }
            // if (chef.media.length === 0 || chef.idProof.length === 0) {
            //   setIsSubmit(true);
            //   isError = true;
            // }
            // if (startTime === '') {
            //   setTimeError('Start time is reqiure');
            //   isError = true;
            // } else if (endTime === '') {
            //   setTimeError('End time is reqiure');
            //   isError = true;
            // } else if (moment(startTime, 'HH:mm') >= moment(endTime, 'HH:mm')) {
            //   setTimeError('Start time must be less than end time');
            //   isError = true;
            // }
            if (!isError) {
              setAvgPriceError('');
              // setTimeError('');
              // setOpenDaysError(false);
              handleSubmitEvent(values);
              setSubmitting(false);
            }
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            setFieldValue,
            handleBlur,
            handleSubmit,
            resetForm,
            validateForm,
            // isSubmitting,
          }) => (
            <form
              onSubmit={e => {
                e.preventDefault();
                checkData();
                handleSubmit(e);
              }}
            >
              <div className="ibox">
                <div className="ibox-content">
                  {/* <div className="row m-b-lg">
                    <div className="col-xs-12">
                      <h2 className="">Vendor Details</h2>
                    </div>
                  </div> */}

                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Vendor Name
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="restaurantName"
                            value={values.restaurantName}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.restaurantName &&
                              touched.restaurantName &&
                              errors.restaurantName}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Vendor Phone Number
                        </label>
                        <div className="col-sm-6 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="retsaurantContact"
                            value={values.retsaurantContact}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.retsaurantContact &&
                              touched.retsaurantContact &&
                              errors.retsaurantContact}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <label
                        className="col-sm-7 col-xs-6 m-t-sm required-field"
                        style={{
                          paddingLeft: '0px',
                          marginRight: '-26px',
                        }}
                      >
                        Vendor Type
                      </label>

                      <Select
                        placeholder="Select Vendors"
                        closeMenuOnSelect={false}
                        value={brandType}
                        isMulti
                        name="brandType"
                        options={brandTypes}
                        onChange={data => {
                          setBrandTypes(data);
                        }}
                      />
                      <div className="error-message">
                        {brandTypeError && 'Required field'}
                      </div>
                    </div>

                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-6 col-xs-12 m-t-sm required-field">
                          Average cost for two
                          <i
                            className="fa fa-info-circle"
                            aria-hidden="true"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="1 sign = 250"
                          />
                        </label>

                        <div className="col-sm-6 col-xs-12">
                          <Rate
                            defaultValue={avgPrice}
                            onChange={setAvgPrice}
                            count="5"
                            allowHalf={false}
                            allowClear={false}
                            style={{ fontSize: 15, marginTop: 8 }}
                            character=""
                          />
                          <div className="error-message">{avgPriceError}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                  {/* <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm">
                          Opening Hours
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <div className="row">
                            <div
                              className="col-lg-6"
                              style={{ marginBottom: '10px' }}
                            >
                              <div
                                className="input-group clockpicker "
                                data-autoclose="true"
                              > */}
                  {/* <input
                                  type="text"
                                  className="form-control"
                                  value="09:30"
                                /> */}
                  {/* <DatePicker
                                  className="form-control"
                                  selected={
                                    new Date(
                                      moment().year(),
                                      moment().month(),
                                      moment().date(),
                                      startTime
                                        ? moment(startTime, 'HH:mm').hour()
                                        : '',
                                      startTime
                                        ? moment(startTime, 'HH:mm').minute()
                                        : '',
                                      0,
                                    )
                                  }
                                  onFocus={e => e.target.blur()}
                                  onChange={date =>
                                    setStartTime(moment(date).format('HH:mm'))
                                  }
                                  showTimeSelect
                                  showTimeSelectOnly
                                  timeIntervals={15}
                                  timeCaption="Time"
                                  dateFormat="h:mm aa"
                                />
                                <span className="input-group-addon">
                                  <span className="fa fa-clock-o" />
                                </span>
                              </div>
                            </div>
                            <div className="col-lg-6">
                              <div
                                className="input-group clockpicker "
                                data-autoclose="true"
                              > */}
                  {/* <input
                                  type="text"
                                  className="form-control"
                                  value="09:30"
                                /> */}
                  {/* <DatePicker
                                  className="form-control"
                                  selected={
                                    new Date(
                                      moment().year(),
                                      moment().month(),
                                      moment().date(),
                                      endTime
                                        ? moment(endTime, 'HH:mm').hour()
                                        : '',
                                      endTime
                                        ? moment(endTime, 'HH:mm').minute()
                                        : '',
                                      0,
                                    )
                                  }
                                  onFocus={e => e.target.blur()}
                                  onChange={date =>
                                    setEndTime(moment(date).format('HH:mm'))
                                  }
                                  showTimeSelect
                                  showTimeSelectOnly
                                  timeIntervals={15}
                                  timeCaption="Time"
                                  dateFormat="h:mm aa"
                                />
                                <span className="input-group-addon">
                                  <span className="fa fa-clock-o" />
                                </span>
                              </div>
                            </div>
                            <div className="error-message">{timeError}</div>
                          </div>
                        </div>
                      </div>
                    </div> */}

                  <div className="row">
                    <div className="form-group usr-form col-sm-12">
                      <div className="row">
                        <label className="col-sm-3 col-xs-12 m-t-sm required-field">
                          Vendor Description
                        </label>
                        <div className="col-sm-9 col-xs-12">
                          {/* <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="retsaurantDescription"
                            value={values.retsaurantDescription}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          /> */}
                          <textarea
                            className="form-control"
                            style={{ height: '200px !important' }}
                            name="retsaurantDescription"
                            value={values.retsaurantDescription}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.retsaurantDescription &&
                              touched.retsaurantDescription &&
                              errors.retsaurantDescription}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    {/* <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm">
                          Opening Days
                        </label>
                        <div className="col-sm-7 col-xs-12"> */}
                    {/* <select
                            className="form-control"
                            name="openDays"
                            value={values.openDays}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          >
                            <option>Monday</option>
                            <option>Tuesday</option>
                          </select> */}
                    {/* <Select
                            placeholder="Select Days"
                            closeMenuOnSelect={false}
                            value={openDays}
                            isMulti
                            options={openDayList}
                            onChange={setOpenDays}
                          />
                          <div className="error-message">
                            {openDaysError && 'Required field'}
                          </div>
                        </div>
                      </div>
                    </div> */}
                  </div>
                  <div className="row" style={{ marginBottom: '20px' }}>
                    <div className="detail-title-a section1  pb-0">
                      <div className="box-heading">
                        <h2
                          className="p-t-lg m-b-md img-bot required-field"
                          style={{ marginBottom: '10px', width: 'fit-content' }}
                        >
                          Vendor Media
                        </h2>
                        <p>The first picture is your profile picture</p>
                      </div>
                      <div className="row" id="kyc-doc">
                        {chef.media &&
                          chef.media.map((item, index) =>
                            index === 0 ? (
                              <div
                                className="col-lg-2 col-md-4 col-sm-3 col-xs-6"
                                key={item.name}
                              >
                                <a target="_blank" href={`${item.name}`}>
                                  <div className="box">
                                    <input
                                      type="file"
                                      className="hidden-file-uploder"
                                      onChange={e => {
                                        if (e.target.files.length) {
                                          const { files } = e.target;
                                          let isError = false;
                                          // eslint-disable-next-line no-restricted-syntax
                                          for (const key in files) {
                                            if (
                                              Object.prototype.hasOwnProperty.call(
                                                files,
                                                key,
                                              )
                                            ) {
                                              const extension = files[key].name
                                                .split('.')
                                                .pop();
                                              if (
                                                !extension.match(
                                                  /(JPG|JPEG|PNG)$/i,
                                                )
                                              ) {
                                                isError = true;
                                                break;
                                              }
                                            }
                                          }

                                          if (!isError) {
                                            dispatch(
                                              showModal({
                                                open: true,
                                                notifyType: 0,
                                                withButton: false,
                                                message: `We are uploading media, 
                                              please wait a while...`,
                                              }),
                                            );
                                            dispatch(
                                              editMediaSaga({
                                                media: [...files],
                                                isDefaultImage: true,
                                              }),
                                            );
                                          } else {
                                            dispatch(
                                              showModal({
                                                open: true,
                                                notifyType: 1,
                                                message: `Please upload image file only`,
                                              }),
                                            );
                                          }
                                        }
                                        e.target.value = '';
                                      }}
                                    />
                                    <img
                                      src={`${item.thumbnail}`}
                                      alt=""
                                      className="myImg"
                                    />
                                  </div>
                                </a>
                              </div>
                            ) : (
                              <div
                                className="col-lg-2 col-md-4 col-sm-3 col-xs-6"
                                key={item.name}
                              >
                                <a target="_blank" href={`${item.name}`}>
                                  <div className="box">
                                    <i
                                      className="fa fa-close upload-remove"
                                      aria-hidden="true"
                                      onClick={e => {
                                        e.preventDefault();
                                        dispatch(
                                          deleteMediaSaga({
                                            name: item.name,
                                            index,
                                          }),
                                        );
                                      }}
                                    />
                                    {item.type === 'image' ? (
                                      <img
                                        src={`${item.thumbnail}`}
                                        alt=""
                                        className="myImg"
                                      />
                                    ) : (
                                      <img
                                        src={`${item.thumbnail}`}
                                        alt=""
                                        className="myImg"
                                      />
                                    )}
                                  </div>
                                </a>
                              </div>
                            ),
                          )}
                        {chef.media.length < 3 && (
                          <div className="col-lg-2 col-md-4 col-sm-3 col-xs-6">
                            <a target="_blank">
                              <div className="box">
                                <input
                                  type="file"
                                  className="hidden-file-uploder"
                                  multiple={
                                    chef.media.length === 1 ||
                                    chef.media.length !== 0
                                  }
                                  onChange={e => {
                                    if (e.target.files.length) {
                                      const { files } = e.target;
                                      let isError = false;
                                      // eslint-disable-next-line no-restricted-syntax
                                      for (const key in files) {
                                        if (
                                          Object.prototype.hasOwnProperty.call(
                                            files,
                                            key,
                                          )
                                        ) {
                                          const extension = files[key].name
                                            .split('.')
                                            .pop();
                                          if (
                                            !extension.match(
                                              /(JPG|JPEG|PNG|MOV|MP4)$/i,
                                            )
                                          ) {
                                            isError = true;
                                            break;
                                          }
                                        }
                                      }
                                      if (!isError) {
                                        dispatch(
                                          showModal({
                                            open: true,
                                            notifyType: 0,
                                            withButton: false,
                                            message: `We are uploading media, 
                                          please wait a while...`,
                                          }),
                                        );
                                        dispatch(
                                          editMediaSaga({
                                            media: [...files],
                                            isDefaultImage:
                                              chef.media.length === 0,
                                          }),
                                        );
                                      } else {
                                        dispatch(
                                          showModal({
                                            open: true,
                                            notifyType: 1,
                                            message: `Please upload image and video file only`,
                                          }),
                                        );
                                      }
                                    }
                                    e.target.value = '';
                                  }}
                                />
                                <i
                                  className="fa fa-plus upload-icon"
                                  aria-hidden="true"
                                />
                              </div>
                            </a>
                          </div>
                        )}
                      </div>
                      {isSubmit && chef.media.length === 0 && (
                        <div className="error-message left-align">
                          Media is required
                        </div>
                      )}
                    </div>
                    <div className="detail-title-a section1  mt-0 p-b-20">
                      <div className="box-heading">
                        <h2
                          className={
                            chef.isApproved
                              ? 'p-t-lg m-b-md img-bot'
                              : 'p-t-lg m-b-md img-bot required-field'
                          }
                          style={{ marginBottom: '10px', width: 'fit-content' }}
                        >
                          Legal Documents
                        </h2>
                        <p style={{ marginBottom: 10 }}>
                          Please upload FSSAI if registered, Cancelled cheque,
                          PAN Card, GST Certificate (if applicable)
                        </p>
                      </div>
                      <div className="row" id="kyc-doc">
                        {chef.idProof &&
                          chef.idProof.map((item, index) => (
                            <div
                              className="col-lg-2 col-md-4 col-sm-3 col-xs-6"
                              key={item.name}
                            >
                              <a target="_blank" href={`${item.name}`}>
                                <div className="box">
                                  {chef.idProof.length > 1 && (
                                    <i
                                      className="fa fa-close upload-remove"
                                      aria-hidden="true"
                                      onClick={e => {
                                        e.preventDefault();
                                        dispatch(
                                          showModal({
                                            open: true,
                                            notifyType: 0,
                                            withButton: false,
                                            message: `We are deleting legal document, 
                                            please wait a while...`,
                                          }),
                                        );
                                        dispatch(
                                          deleteIdproofSaga({
                                            name: item.name,
                                            index,
                                          }),
                                        );
                                      }}
                                    />
                                  )}
                                  {item.type === 'image' ? (
                                    <img
                                      src={`${item.thumbnail}`}
                                      alt=""
                                      className="myImg"
                                    />
                                  ) : (
                                    <img
                                      src={
                                        item.name.includes('pdf')
                                          ? require('../../../assets/img/pdf.jpg')
                                          : require('../../../assets/img/doc.jpg')
                                      }
                                      alt=""
                                      className="myImg idproof-doc"
                                    />
                                  )}
                                </div>
                              </a>
                            </div>
                          ))}
                        {chef.isApproved &&
                          chef.idProof &&
                          chef.idProof.length === 0 && (
                            <div className="noDocFiles">
                              No Legal Documents Found
                            </div>
                          )}
                        <div className="col-lg-2 col-md-4 col-sm-3 col-xs-6">
                          <a target="_blank">
                            <div className="box">
                              <input
                                type="file"
                                className="hidden-file-uploder"
                                multiple
                                onChange={e => {
                                  if (e.target.files.length) {
                                    const { files } = e.target;
                                    let isError = false;
                                    // eslint-disable-next-line no-restricted-syntax
                                    for (const key in files) {
                                      if (
                                        Object.prototype.hasOwnProperty.call(
                                          files,
                                          key,
                                        )
                                      ) {
                                        const extension = files[key].name
                                          .split('.')
                                          .pop();
                                        if (!extension.match(idProofRegx)) {
                                          isError = true;
                                          break;
                                        }
                                      }
                                    }
                                    if (!isError) {
                                      dispatch(
                                        showModal({
                                          open: true,
                                          notifyType: 0,
                                          withButton: false,
                                          message: `We are uploading legal document, 
                                          please wait a while...`,
                                        }),
                                      );
                                      dispatch(
                                        editIdproofSaga({
                                          idProof: [...files],
                                        }),
                                      );
                                    } else {
                                      dispatch(
                                        showModal({
                                          open: true,
                                          notifyType: 1,
                                          message: `Please upload doc, pdf or image file only`,
                                        }),
                                      );
                                    }
                                  }
                                  e.target.value = '';
                                }}
                              />
                              {/* <img
                                src={require('../../../assets/img/plus.svg')}
                                alt=""
                                className="myImg"
                              /> */}
                              <i
                                className="fa fa-plus upload-icon"
                                aria-hidden="true"
                              />
                            </div>
                          </a>
                        </div>
                      </div>
                      {isSubmit && chef.idProof.length === 0 && (
                        <div className="error-message left-align">
                          Legal Documents are required
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Payment Options
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <select
                            className="form-control"
                            name="paymentOptions"
                            value={values.paymentOptions}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          >
                            <option value="COD">COD</option>
                            <option value="online">Online</option>
                            <option value="both">Both</option>
                          </select>
                          <div className="error-message">
                            {errors.paymentOptions &&
                              touched.paymentOptions &&
                              errors.paymentOptions}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Accept Order Type
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <select
                            className="form-control"
                            name="orderType"
                            value={values.orderType}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          >
                            <option value="PickUpOnly">Pick up only</option>
                            <option value="DeliveryOnly">Delivery only</option>
                            <option value="both">Both</option>
                          </select>
                          <div className="error-message">
                            {errors.orderType &&
                              touched.orderType &&
                              errors.orderType}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Address Line 1
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="houseNo"
                            value={values.houseNo}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.houseNo &&
                              touched.houseNo &&
                              errors.houseNo}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm">
                          Address Line 2
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="apartment"
                            value={values.apartment}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.apartment &&
                              touched.apartment &&
                              errors.apartment}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Landmark
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="landmark"
                            value={values.landmark}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.landmark &&
                              touched.landmark &&
                              errors.landmark}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Area
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="area"
                            value={values.area}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.area && touched.area && errors.area}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Country
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="country"
                            value={values.country}
                            disabled
                            // onChange={handleChange}
                            // onBlur={handleBlur}
                          />
                          {/* <div className="error-message">
                            {errors.country &&
                              touched.country &&
                              errors.country}
                          </div> */}
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          State
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          {/* <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="state"
                            value={values.state}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          /> */}
                          <select
                            className="form-control"
                            name="state"
                            value={values.state}
                            onChange={e => {
                              handleChange(e);
                              const stateObj = getStateByName(e.target.value);
                              setCityList(getCitiesOfState(stateObj.id));
                              setFieldValue('city', '');
                            }}
                            onBlur={e => {
                              handleBlur(e);
                              const stateObj = getStateByName(e.target.value);
                              setCityList(getCitiesOfState(stateObj.id));
                            }}
                          >
                            <option value="">Select state</option>
                            {stateList.map(item => (
                              <option key={item.id} value={item.name}>
                                {item.name}
                              </option>
                            ))}
                          </select>
                          <div className="error-message">
                            {errors.state && touched.state && errors.state}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          City
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          {/* <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="city"
                            value={values.city}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          /> */}
                          <select
                            className="form-control"
                            name="city"
                            value={values.city}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          >
                            <option value="">Select city</option>
                            {cityList.map(item => (
                              <option key={item.id} value={item.name}>
                                {item.name}
                              </option>
                            ))}
                          </select>
                          <div className="error-message">
                            {errors.city && touched.city && errors.city}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group usr-form col-sm-6">
                      <div className="row">
                        <label className="col-sm-5 col-xs-12 m-t-sm required-field">
                          Pincode
                        </label>
                        <div className="col-sm-7 col-xs-12">
                          <input
                            type="text"
                            className="form-control"
                            placeholder=""
                            name="pincode"
                            value={values.pincode}
                            onChange={handleChange}
                            onBlur={handleBlur}
                          />
                          <div className="error-message">
                            {errors.pincode &&
                              touched.pincode &&
                              errors.pincode}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group usr-form col-sm-12">
                      <div className="row">
                        <div
                          className="col-sm-12 col-xs-12"
                          style={{ marginTop: '11px' }}
                        >
                          <button
                            className="btn btn-primary"
                            type="button"
                            disabled={isMapLoading}
                            style={{ marginBottom: '25px', float: 'right' }}
                            onClick={async () =>
                              getCoordinatesFromApi(
                                await validateForm(values),
                                values,
                              )
                            }
                          >
                            Load map from address
                          </button>
                          <p style={{ marginTop: '7px' }}>
                            Please load the marker on the map by click on the
                            button
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form-group col-sm-12 google-map-react">
                      <Map
                        isEditable
                        coordinates={coordinates}
                        getLatLng={getLatLng}
                        isReloadMap={isReloadMap}
                        setIsReloadMap={setIsReloadMap}
                      />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 error-message">{errorMsg}</div>
                  </div>
                  <div className=" text-center">
                    <button
                      type="submit"
                      disabled={isDisable}
                      data-toggle="modal"
                      data-modal="dismiss"
                      data-target="#cong-modal"
                      className="btn btn-primary m-5"
                    >
                      {isApproved ? 'Update' : 'Save & Next'}
                    </button>
                    {isApproved && (
                      <button
                        type="button"
                        disabled={isDisable}
                        className="btn btn-primary"
                        onClick={() => {
                          resetForm();
                          setIsReloadMap(true);
                          setCoordinates({
                            latitude: chef.lat || '',
                            longitude: chef.long || '',
                          });
                        }}
                      >
                        Reset
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </form>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default SetTokenHeader(RestaurantDetails, axios);
